module.exports = {
    init(React) {
        this.createState = this.createState.bind(React);
        React.createState = this.createState;
    },
    createState(initialState) {
        const [state, setDataState] = this.useState(initialState);
        const setState = (data, callback) => {
            if (callback && typeof callback == 'function') {
                ref.current.callback = callback;
            }
            if (callback) {
                ref.current.state = { ...state, ...data };
                setDataState(ref.current.state);
            } else {
                return new Promise((resolve, reject) => {
                    ref.current.callback = resolve;
                    ref.current.state = { ...state, ...data }
                    setDataState(ref.current.state);
                })
            }
        }
        const ref = this.useRef({
            state,
            setState
        });
        this.useEffect(() => {
            if (ref.current && ref.current.callback)
                ref.current.callback(state);
        }, [state])

        return ref.current
    }
}
